:root {
  --bleu_fonce: #171717;
  --bleu_clair: #666;
  --sable: #eed5a7;
  --beige: #f9eee0;
  --pomme: #7dc4a3;
  --blanc: #fff;
  --PFormalScript: "Petit Formal Script";
  --Larsseit: Larsseit;
}

* {
  color: #fff;
  background-color: #171717;
  margin: 0;
  padding: 0;
}

.navigation {
  z-index: 15;
  position: fixed;
}

svg .blinkit {
  animation: 1.5s linear infinite col;
}

@keyframes col {
  0%, 2% {
    fill: none;
  }

  3% {
    fill: #fff;
  }

  75%, 100% {
    fill: none;
  }
}

.accueil {
  margin: 0 50px;
}

.accueil h1 {
  text-align: center;
}

.accueil .fleche-accueil {
  position: fixed;
  top: 90%;
  right: 50%;
}

.page-presentation {
  width: 70%;
  margin: 0 auto;
}

.page-presentation p {
  color: #999;
  font-family: IBM Plex Mono, monospace;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
}

.page-presentation h1 {
  text-align: center;
  margin-bottom: 4%;
  font-family: Roboto Mono, monospace;
  font-weight: bold;
}

.page-presentation h3 {
  margin-bottom: 1%;
  font-family: Roboto Mono, monospace;
}

.presentation p {
  margin: 40px 0;
}

.titre-passion h1 {
  margin-bottom: 0;
}

.passions {
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.passions h3 {
  text-align: center;
}

.passions img {
  height: 32vh;
  margin: 0 auto 10px;
  display: block;
}

.page-cv {
  width: 80%;
  margin: 0 auto;
}

.CV .column-gauche img {
  width: 5%;
}

.header {
  background-color: var(--bleu_fonce);
  z-index: 5;
  opacity: .95;
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 7px 29px #64646f33;
}

.main_container_header {
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 100%;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

.header__logo img {
  max-height: 50px;
  padding-right: 80px;
}

.header__nav__menu {
  align-items: center;
  display: flex;
}

.header__nav__menu__link {
  margin-right: 40px;
  overflow-y: hidden;
}

.header__nav__menu__link:last-of-type {
  margin-right: initial;
}

.header__nav__menu__link a {
  color: var(--blanc);
  font-family: Arial;
  font-weight: 600;
  text-decoration: none;
  position: relative;
}

.header__nav__menu__link a:after {
  content: "";
  background-color: var(--bleu_fonce);
  width: 0;
  height: 2px;
  transition: all .2s ease-in-out;
  position: absolute;
  bottom: -5px;
  right: 0;
}

.header__nav__menu__link a:hover:after {
  width: 100%;
  left: 0;
}

.header__burger {
  display: none;
}

.header__burger svg {
  stroke: #fff;
  width: 50px;
  height: 50px;
}

.header__nav__close {
  display: none;
}

@media screen and (width <= 924px) {
  .header__burger {
    cursor: pointer;
    display: block;
  }

  .header__nav {
    z-index: 20;
    background-color: var(--bleu_fonce);
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 100vh;
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    box-shadow: -10px 4px 8px #0003;
  }

  .header__nav.open {
    animation: .3s ease-in-out forwards transformMenu;
    display: flex;
  }

  @keyframes transformMenu {
    0% {
      transform: translateX(100%);
    }

    100% {
      transform: initial;
    }
  }

  .header__nav__menu {
    flex-direction: column;
    align-items: flex-start;
  }

  .header__nav__menu__link {
    margin-bottom: 20px;
    margin-right: 0;
  }

  .header__nav__menu__link a {
    font-size: 25px;
  }

  .header__nav__close {
    cursor: pointer;
    display: block;
  }

  .header__nav__close svg {
    stroke: #fff;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .overlay-menu-mobile {
    background-color: var(--bleu_fonce);
    width: 100vw;
    height: 100vh;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
  }

  .overlay-menu-mobile.open {
    display: block;
  }
}

